"use client";

import { ArrowRight } from "lucide-react";
import { useState } from "react";

export function WaitlistForm() {
    const [email, setEmail] = useState("");

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        // Handle submission
        console.log("Joined waitlist:", email);
    };

    return (
        <form onSubmit={handleSubmit} className="relative group w-full">
            <div className="absolute -inset-1 bg-gradient-to-r from-electric-blue to-neon-purple rounded-full opacity-30 blur-md group-hover:opacity-60 transition duration-500" />
            <div className="relative flex items-center bg-black/40 backdrop-blur-md border border-white/10 rounded-full p-1.5 shadow-2xl">
                <input
                    type="email"
                    placeholder="Enter your email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    className="flex-1 bg-transparent px-6 py-3 text-white placeholder-white/40 focus:outline-none min-w-0"
                    required
                />
                <button
                    type="submit"
                    className="px-6 py-2.5 bg-white text-black font-medium rounded-full hover:bg-white/90 transition-colors flex items-center gap-2 whitespace-nowrap"
                >
                    Get Early Access
                    <ArrowRight className="size-4" />
                </button>
            </div>
        </form>
    );
}
