"use client";

import { motion } from "framer-motion";

export function BridgeVisual() {
    return (
        <div className="h-32 w-full flex items-center justify-center relative my-8">
            {/* Left Dot */}
            <motion.div
                initial={{ scale: 0, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                transition={{ duration: 0.5, delay: 0.2 }}
                className="size-4 bg-white rounded-full absolute left-[30%]"
            />

            {/* Right Dot */}
            <motion.div
                initial={{ scale: 0, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                transition={{ duration: 0.5, delay: 0.2 }}
                className="size-4 bg-white rounded-full absolute right-[30%]"
            />

            {/* Connecting Bridge */}
            <motion.div
                initial={{ scaleX: 0, opacity: 0 }}
                animate={{ scaleX: 1, opacity: 1 }}
                transition={{ duration: 0.8, delay: 0.6, ease: "easeInOut" }}
                className="h-[2px] bg-gradient-to-r from-electric-blue to-neon-purple absolute left-[30%] right-[30%] origin-left shadow-[0_0_15px_var(--color-electric-blue)]"
            />

            {/* Pulse Effect */}
            <motion.div
                animate={{
                    opacity: [0, 0.5, 0],
                    left: ["30%", "70%"]
                }}
                transition={{
                    duration: 2,
                    repeat: Infinity,
                    ease: "linear",
                    repeatDelay: 0.5
                }}
                className="absolute h-[4px] w-8 bg-white/80 blur-sm rounded-full top-[calc(50%-2px)] pointer-events-none"
            />
        </div>
    );
}
